/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
  scroll-behavior: smooth;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Outfit", sans-serif;
  font-size: 1.8rem;
  background-color: rgb(17.85, 17.85, 17.85);
}

a {
  text-decoration: none;
}

img {
  width: 100%;
  height: auto;
}

.button {
  background-color: #6C45F3;
  padding: 1.5rem 4rem;
  color: #ffffff;
  font-size: 1.8rem;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  width: 100%;
  display: inline-block;
}
.button:hover {
  background-color: rgb(89.0681818182, 45.0454545455, 241.4545454545);
  cursor: pointer;
}

.logo {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 5rem;
  height: 5rem;
  padding: 0.5rem;
  background-color: #ffffff;
  border-radius: 100%;
  transition: all 0.3s ease-in-out;
}
.logo:hover {
  cursor: pointer;
  background-color: rgb(123.1454545455, 88.1636363636, 244.2363636364);
}
.logo__image {
  width: 65%;
}

.wrapper {
  overflow: hidden;
  width: 100%;
}

h1, h2, h3 {
  font-family: "Outfit", sans-serif;
  font-weight: #000000;
  margin: 2rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

.text-center {
  text-align: center;
}

[class$=__heading] {
  font-family: "Outfit", sans-serif;
  font-weight: #000000;
  margin: 2rem 0;
  margin: 6rem 0 3rem 0;
  text-align: center;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  background: #0a0a1f;
  position: relative;
  font-family: Arial, sans-serif;
  color: white;
}
body h1 {
  font-size: 2.5rem;
  text-align: center;
  z-index: 10;
}

@keyframes pulse {
  0%, 100% {
    transform: scale(0.8);
    opacity: 0.3;
  }
  50% {
    transform: scale(1);
    opacity: 1;
  }
}
@keyframes float {
  0%, 100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-20px);
  }
}
.glow {
  position: absolute;
  border-radius: 50%;
  filter: blur(80px);
  opacity: 0.6;
  z-index: -1;
  animation: pulse 5s ease-in-out infinite alternate, float 12s ease-in-out infinite;
}
.glow.glow1 {
  width: 35%;
  height: 35%;
  background: radial-gradient(circle, rgba(150, 80, 255, 0.7), transparent 70%);
  top: 10%;
  left: 15%;
  animation-delay: 0s, 0s;
}
.glow.glow2 {
  width: 30%;
  height: 30%;
  background: radial-gradient(circle, rgba(80, 200, 255, 0.6), transparent 70%);
  top: 60%;
  left: 30%;
  animation-delay: 2s, 4s;
}
.glow.glow3 {
  width: 30%;
  height: 30%;
  background: radial-gradient(circle, rgba(255, 80, 150, 0.5), transparent 70%);
  top: 30%;
  left: 50%;
  animation-delay: 1s, 6s;
}
.glow.glow4 {
  width: 25%;
  height: 25%;
  background: radial-gradient(circle, rgba(255, 200, 80, 0.4), transparent 70%);
  top: 75%;
  left: 20%;
  animation-delay: 3s, 8s;
}
.glow.glow5 {
  width: 28%;
  height: 28%;
  background: radial-gradient(circle, rgba(100, 255, 180, 0.4), transparent 70%);
  top: 40%;
  left: 10%;
  animation-delay: 4s, 10s;
}
.glow.glow6 {
  width: 22%;
  height: 22%;
  background: radial-gradient(circle, rgba(255, 100, 255, 0.5), transparent 70%);
  top: 5%;
  left: 60%;
  animation-delay: 5s, 12s;
}

.navbar {
  position: sticky;
  display: flex;
  flex-direction: column-reverse;
  gap: 2.5rem;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  padding: 1rem 10rem;
  margin: 0 auto;
  height: auto;
  top: 0;
  left: 0;
  z-index: 99;
  box-shadow: -1px 3px 8px -1px rgba(0, 0, 0, 0.2);
}
@media (min-width: 768px) {
  .navbar {
    flex-direction: row;
    padding: 1rem 3rem;
  }
}
.navbar::before {
  position: absolute;
  content: "";
  width: 100%;
  height: 100%;
  background-color: rgba(16, 18, 27, 0.4);
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  left: 0;
  bottom: 0;
  z-index: 0;
  border-bottom: 1px solid rgba(113, 119, 144, 0.25);
}
.navbar__menu {
  position: relative;
  z-index: 2;
  display: flex;
  flex-direction: column;
  list-style: none;
  gap: 3rem;
  align-items: center;
  padding: 0;
  width: 100%;
}
@media (min-width: 768px) {
  .navbar__menu {
    flex-direction: row;
    width: auto;
  }
}
.navbar__menu--item {
  color: #ffffff;
}
.navbar .logo {
  position: relative;
  z-index: 2;
}

.active {
  background-color: #6C45F3;
  color: #ffffff;
  padding: 1rem 2rem;
  border-radius: 10rem;
}

.header {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 95%;
  margin: 0 auto;
  margin-top: 5rem;
  gap: 5rem;
  border-radius: 3rem;
  padding: 5rem;
}
.header::before {
  position: absolute;
  content: "";
  width: 100%;
  height: 100%;
  background-color: rgba(16, 18, 27, 0.4);
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  left: 0;
  bottom: 0;
  z-index: 0;
  border: 1px solid rgba(113, 119, 144, 0.25);
  border-radius: 1rem;
}
@media (min-width: 1024px) {
  .header {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    justify-content: space-between;
    column-gap: 2rem;
  }
}
.header__title {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: start;
  position: relative;
  z-index: 2;
}
.header__title h1 {
  color: #ffffff;
  width: 90%;
  font-size: 3.8rem;
  font-weight: 700;
  font-family: "Rubik", sans-serif;
  margin: 0;
  text-align: left;
}
.header__title h1 span {
  color: #6C45F3;
}
@media (min-width: 1024px) {
  .header__title h1 {
    font-size: 5.5rem;
  }
}
.header__title p {
  color: #ffffff;
  font-weight: 400;
  font-family: "Outfit", sans-serif;
  margin-bottom: 5rem;
}
.header__imagen {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
  position: relative;
  z-index: 2;
}
.header__imagen picture {
  width: 80%;
  border-radius: 3rem;
}

.contenido-principal {
  position: relative;
  padding: 8rem 5rem;
  margin: 0 auto;
  margin-top: 5rem;
  display: flex;
  justify-content: center;
  width: 90%;
}
.contenido-principal::before {
  position: absolute;
  content: "";
  width: 100%;
  height: 100%;
  background-color: rgba(16, 18, 27, 0.4);
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  left: 0;
  bottom: 0;
  z-index: 0;
  border: 1px solid rgba(113, 119, 144, 0.25);
  border-radius: 1rem;
}

.services__cards {
  position: relative;
  z-index: 2;
  display: grid;
  gap: 5rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 768px) {
  .services__cards {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .services__cards {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

.services__card {
  padding: 3rem;
  display: flex;
  flex-direction: column;
  background-color: #6C45F3;
  height: 35rem;
  width: 100%;
  color: #ffffff;
}
.services__card i {
  font-size: 5rem;
  margin-bottom: 5rem;
}

.tecnologias {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 90%;
  margin: 3rem auto 0 auto;
  padding: 3rem;
  border-radius: 1rem;
  overflow: hidden;
  color: rgb(242.8571428571, 242.8571428571, 242.8571428571);
}
.tecnologias::before {
  position: absolute;
  content: "";
  width: 100%;
  height: 100%;
  background-color: rgba(16, 18, 27, 0.4);
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  left: 0;
  bottom: 0;
  z-index: 0;
  border: 1px solid rgba(113, 119, 144, 0.25);
  border-radius: 1rem;
}
.tecnologias__titulo, .tecnologias__parrafo {
  margin-left: 3rem;
  position: relative;
  z-index: 2;
}
.tecnologias__lista {
  place-items: center;
  list-style: none;
  margin: 0;
  padding: 0;
  width: 100%;
  justify-content: space-evenly;
  padding: 0.5rem 1rem;
  margin-top: 5rem;
  display: grid;
  gap: 1rem;
  grid-template-columns: repeat(2, 1fr);
}
@media (min-width: 768px) {
  .tecnologias__lista {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(4, 1fr);
  }
}
@media (min-width: 1024px) {
  .tecnologias__lista {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(8, 1fr);
  }
}
.tecnologias__nombre {
  position: relative;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  clip-path: polygon(30% 0%, 70% 0%, 100% 30%, 100% 70%, 70% 100%, 30% 100%, 0% 70%, 0% 30%);
  background-color: rgb(76.5, 76.5, 76.5);
  width: 12rem;
  height: 12rem;
  padding: 1rem;
  font-weight: bold;
}
.tecnologias__nombre p, .tecnologias__nombre i, .tecnologias__nombre svg {
  position: relative;
  z-index: 3;
  text-shadow: 2px 2px 5px rgba(0, 0, 0, 0.8549019608);
  margin: 0;
}
.tecnologias__nombre i {
  font-size: 32px;
  margin-bottom: 5px;
}
.tecnologias__image-bg {
  position: absolute;
  width: 100%;
  height: 100%;
  object-fit: cover;
  filter: blur(6px);
}